import React from 'react';
import PropTypes from 'prop-types';
import Link from 'next/link';
import jsCookie from 'js-cookie';
import _ from 'lodash';

import { MenuItem, Select, ButtonBase, Grid } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';

import Head from '../components/head';
import BaseContainer from '../components/base-container';
import { BaseNavBar, Loader } from '../components/common';
import t from '../libs/i18n-util';
import colors from '../libs/styleguide/colors';

const styles = {
  gameList: {
    marginTop: 20,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  grid: {
    maxWidth: 1200,
  },
  imgButton: {
    display: 'block',
    margin: 'auto',
    width: '100%',
    maxWidth: 400,
    '& img' : {
      width: '100%',
    },
  },
  selector: { width: '100%' },
  selectorRoot: {
    color: 'white',
    backgroundColor: colors.darkGrey,
    borderRadius: 4,
    padding: 10,
  },
  selectorIcon: {
    color: colors.palePeach,
  },
  selectorWrapper: {
    display: 'flex',
    justifyContent: 'center',
    width: '100%',
    maxWidth: 400,
  }
};

class Home extends React.Component {
  state = {
    language: 'en-US',
  }

  handleChange = event => {
    this.setState({ language: event.target.value });
    jsCookie.set('hl', event.target.value, { expires: 365 });
  }

  componentDidMount() {
    const language = jsCookie.get('hl');
    if (_.isNil(language) === false) {
      this.setState({ language });
    } else {
      jsCookie.set('hl', this.state.language, { expires: 365 });
    }
    global.analytics.page('Home');
  }

  render () {
    const { classes } = this.props;
    return (
      <Loader>
        <Head title="Home" />
        <BaseNavBar middle={t('casio_lobby')}/>
        <BaseContainer background={false}>
          <div className={classes.gameList}>
            <div className={classes.selectorWrapper}>
              <Select
                className={classes.selector}
                classes={{
                  root: classes.selectorRoot,
                  icon: classes.selectorIcon,
                }}
                value={this.state.language}
                onChange={this.handleChange}
                disableUnderline
              >
                <MenuItem value="en-US">ENGLISH</MenuItem>
                <MenuItem value="zh-TW">正體中文</MenuItem>
                <MenuItem value="zh-CN">简体中文</MenuItem>
              </Select>
            </div>
            <br/>
            <Grid classes={{ container: classes.grid }} container spacing={8} justify="center" alignItems="center">
              <Grid item md={4}>
                <Link href="/dice">
                  <ButtonBase className={classes.imgButton}>
                    <img src="/static/img/lobby/dice.svg" />
                  </ButtonBase>
                </Link>
              </Grid>
              <Grid item md={4}>
                <Link href="/baccarat">
                  <ButtonBase className={classes.imgButton}>
                    <img src="/static/img/lobby/baccarat.svg" />
                  </ButtonBase>
                </Link>
              </Grid>
              <Grid item md={4}>
                <Link href="/pk10">
                  <ButtonBase className={classes.imgButton}>
                    <img src="/static/img/lobby/pk10.svg" />
                  </ButtonBase>
                </Link>
              </Grid>
            </Grid>
          </div>
        </BaseContainer>
      </Loader>
    );
  }
}

Home.propTypes = {
  classes: PropTypes.object,
};

export default withStyles(styles)(Home);
